<template>
    <div id="wechat">
        <ul class="wechat-list"  v-if="$route.path == '/Wechat' || $route.path == '/' " >
          <router-link tag="li" :to=" { path: '/wechat/dialogue', query:{ mid:baseMsgObj.mid,name:baseMsgObj.group_name || (baseMsgObj.user[0].remark || baseMsgObj.user[0].nickname)}} "    v-for="(baseMsgObj,index) in getMsgList"  :key="baseMsgObj.mid" >
            <div class="item-box">
              <div class="pic">
                <img v-for="(item,dex) in baseMsgObj.user" :src="item.headerUrl">
              </div>
              <div class="info">
                <p v-if="baseMsgObj.type==='group'">{{baseMsgObj.group_name}}</p>
                <p v-else>{{baseMsgObj.user[0].remark||baseMsgObj.user[0].nickname}}</p>
                <span v-if="baseMsgObj.type==='group'">{{baseMsgObj.msg[baseMsgObj.msg.length-1].name}}:</span>
                <span>{{baseMsgObj.msgmsg[baseMsgObj.msgmsg.length-1].text}}</span>
              </div>
            </div>

          </router-link>
        </ul>
        <router-view ></router-view>

    </div>
</template>

<script>

    export default {
        name: 'app',
        components: {

        },
        computed:{
            getMsgList(){

                return this.$store.state.msgList.baseMsg;
            }
        },

    }
</script>

<style>
.wechat-list li{
    height: 64px;
    padding: 8px;
    border-bottom: 1px solid #dbdbdb;
    background-color: #ffffff;
}
.wechat-list li .pic{

    float: left;
}
.wechat-list li .info{
    float: left;
    margin-left: 10px;
    height: 25px;
    line-height: 25px;
    font-size: 16px;
}
.wechat-list .info span{
    font-size: 14px;
    color: #888888;
}
.wechat-list li img{
    width: 48px;
    height: 48px;
    border-radius: 6px;
}


</style>



